<template>
  <!-- 购买任务 -->
  <div class="tasks">
    <el-card class="tasks-card form-card" :body-style="{ padding: '0 0 20px'}">
      <div slot="header" class="clearfix">
        <span class="form-title">{{ $t('tasks.purchaseTasks') }}</span>
      </div>
      <el-row class="tasks-info">
        <el-col :lg="24">
          <h5>{{ $t('tasks.tasksRemind') }}</h5>
          <h5>{{ $t('tasks.adressTitle') }}</h5>
          <h5 style="word-break: break-all;">{{ purchaseTask.tasksAdress }}</h5>
          <h5>{{ $t('tasks.tasksPurchase') }}{{ purchaseTask.tasksPurchase === undefined ? 0 : purchaseTask.tasksPurchase}} ETH</h5>
          <h5>{{ $t('tasks.hadPurchase') }}{{ purchaseTask.hadPurchase === undefined ? 0 : purchaseTask.hadPurchase}} ETH</h5>
          <h5>{{ $t('tasks.needSpend') }}{{ purchaseTask.needSpend === undefined ? 0 : purchaseTask.needSpend}} ETH</h5>
          <h5>{{ $t('tasks.lockNumber') }} {{ purchaseTask.lockStraight === undefined ? 0 : purchaseTask.lockStraight }} ETH</h5>
        </el-col>
      </el-row>
      <div class="card-middle">
        <el-row type="flex" justify="space-between">
          <el-col :span="8">
            <el-image class="eth-img" :src="ethUrl" fit="fill"></el-image>
          </el-col>
          <el-col :span="8">
            <el-input
              :placeholder="$t('purchase.placeholder')"
              v-model="inputPurchase"
              type="number"
              class="input-style"
            />
          </el-col>
          <el-col :span="8">
            <el-image class="yellowkey-img" :src="yellowkeyUrl" fit="fill"></el-image>
          </el-col>
        </el-row>
        <div class="box-btn">
          <el-button round @click="purchaseBuy()">{{ $t('commom.btnPurchase') }}</el-button>
        </div>
      </div>
    </el-card>

    <el-card class="support-card form-card bottom-card" :body-style="{ padding: '0px'}">
      <div slot="header" class="clearfix">
        <span class="form-title">{{$t('tasks.support')}}</span>
      </div>
      <div>
        <el-table :data="recommendRecord" style="width: 100%" height="261" size="mini">
          <el-table-column
            prop="oldTime"
            :label="$t('commom.date')"
             width="90"
            sortable
            align="center"
          ></el-table-column>
          <el-table-column
            prop="eth"
            label="ETH"
             width="80"
            sortable
            align="center"
          ></el-table-column>
          <el-table-column
            prop="leftDays"
            :label="$t('commom.leftday')"
            width="80"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="address"
            :label="$t('commom.address')"
            align="center"
            show-overflow-tooltip
          ></el-table-column>
          <el-table-column
            label="supported"
            align="center"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              {{ scope.row.isSupported }}
            </template>
          </el-table-column>
          <el-table-column
            fixed="right"
            align="center"
            :label="$t('commom.action')"
            width="70"
          >
            <template slot-scope="scope">
              <el-button
                @click="supportReward(scope.row.oldTime, scope.row.isSupported, scope.row.index, scope.row.address, scope.row.eth)"
                type="text"
                size="mini"
                class="action-btn"
              >{{$t('tasks.supportAction') }}</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
import { checkUrlParam } from '../Mixin/checkUrlParam'
import {urlFilter} from '../Mixin/urlFilter'
import ethUtils from '../utils/ethUtils'
import store from '../store/index'
export default {
  name: 'Tasks',
  mixins: [checkUrlParam, urlFilter],
  data () {
    return {
      ethUrl: require('../assets/img/ETH.png'),
      yellowkeyUrl: require('../assets/img/yellowkey.png'),
      // tasksAdress: '0x0000000000000000000000000000000000000000',
      tasksAndBuyOrders: [{
        date: '2015-05-02',
        eth: '123',
        address: '378fdfecd3a3befd3fdfdb4812f5bde2301947',
        leftdays: '3',
        action: 'succeed'
      }, {
        date: '2019-05-02',
        eth: '2456',
        address: '378fdfecd3a3befd3fdfdb4812f5bde2301947',
        leftdays: '2',
        action: 'failed'
      }],
      inputPurchase: 0
    }
  },
  computed: {
    purchaseTask () {
      const purchaseTasksInfo = this.$store.state.contract.purchaseTasksInfo
      return purchaseTasksInfo
    },
    recommendRecord () {
      const record = this.$store.state.contract.recommendRecord
      return record // todo 已注入页面  注意如果传过来空数组可能会报，prototype undefinde
    },
    commitMark: function () {
      return this.$store.state.metamask.commitMark
    }
  },
  watch: {
    commitMark: function (newVal) {
      // const referralAddress = this.checkUrlParam()
      // if (!referralAddress) {
      //   // alert('通过邀请链接的才具有参与资格')
      //   return
      // }
      const status = this.$store.state.metamask.status
      if (status === 1) {
        console.log('watch开始task页面执行获取数据=============')
        this.$store.dispatch('contract/getPurchaseTasksInfo')
        this.$store.dispatch('contract/getRecommendRecord')
      }
    }
  },
  methods: {
    async purchaseBuy () { // purchase页面 purchase操作，buy接口 todo 没有使用hex进制, 需要测试
      const data = {} // 交易数据
      data.referralAddress = this.checkUrlParam()
      if (!data.referralAddress) {
        // alert('通过邀请链接的才具有参与资格')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '通过邀请链接的才具有参与资格',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // 非正过滤
      data.value = this.inputPurchase
      this.inputPurchase = 0 // 获取输入框中的值后清空输入框
      if (data.value <= 0) {
        // alert('请输入有效金额...')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '请输入有效金额...',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // 精度过滤
      const decimalLimit = this.$ethConf.decimalLimit
      const len = data.value.toString().split('.').length
      if (len === 2) {
        if (data.value.toString().split('.')[1].length > decimalLimit) {
          // alert('交易精度过低，请输入有效数据')
          this.$notify({
            title: this.$t('commom.warning'),
            message: '交易精度过低，请输入有效数据',
            position: 'top-left',
            type: 'warning'
          })
          return
        }
      }
      const status = this.$store.state.metamask.status
      if (status !== 1) {
        // alert('请登陆钱包')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '请登陆钱包',
          type: 'warning'
        })
      } else {
        const myProvider = this.$myProvider // todo 使用myProvider，测试通过的话，可以删除 vuex networkVersion
        const networkVersion = myProvider.networkVersion
        if (networkVersion !== this.$ethConf.networkVersion) { // 过滤，非指定网络交易
          // alert('请将metamask网络切换至主网络')
          this.$notify({
            title: this.$t('commom.warning'),
            message: '请将metamask网络切换至主网络',
            type: 'warning'
          })
        } else {
          const contractRes = this.$myContracts && this.$myContracts.contractRes // Resonance合约地址 todo 标记 引用合约
          const myWeb3 = this.$myWeb3
          data.gasPrice = await myWeb3.eth.getGasPrice()
          data.gasLimit = this.$ethConf.gasLimit // gaslimit
          if (contractRes) {
            data.from = myProvider.selectedAddress
            console.warn('task页面合约调用的参数==========', data)
            contractRes.methods.buy(data.referralAddress).send({
              from: data.from,
              gasPrice: data.gasPrice,
              gas: data.gasLimit,
              value: ethUtils.ethToWei(data.value)
            }, function (error, result) {
              if (error) {
                console.error('purchase交易发生错误', error)
              } else {
                console.warn('purchase交易返回直', result) // 返回交易hash
                // 更新purchaseTasksInfos数据 7/24
                store.dispatch('contract/getPurchaseTasksInfo')
              }
            })
          }
        }
      }
    },
    // 市场扶持奖励 接口：supportSubordinateAddress 合约地址：Resonance合约地址
    async supportReward (time, isSupported, index, subordinateAddress, value) { // todo time: 过滤，距离当前时间超过三天的不可已扶持；index:索引， subordinateAddress下级地址， 即getRecommendRecord接口返回的直推地址; value的10%是扶持的数量
      // todo 审核是否已经扶持过
      console.log('获取的issupported数据是', isSupported)
      if (isSupported) { // true已扶持，false未扶持
        this.$notify({
          title: this.$t('commom.warning'),
          message: '您已扶持，不可二次扶持',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      // todo 审核时间是否已过期
      const supportTimeLimit = this.$ethConf.supportTimeLimit
      const currentTime = new Date()
      console.log('-----------------------市场扶持获取的时间', time, typeof time)
      const oldTime = new Date(time)
      console.log('获取的时间ms数是', oldTime.getTime())
      if ((currentTime.getTime() - oldTime.getTime()) > supportTimeLimit) {
        this.$notify({
          title: this.$t('commom.warning'),
          message: '三天的可扶持时间已过，不可再次扶持',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      const data = {} // 交易数据
      const referralAddress = this.checkUrlParam()
      if (!referralAddress) {
        // alert('通过邀请链接的才具有参与资格')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '通过邀请链接的才具有参与资格',
          position: 'top-left',
          type: 'warning'
        })
        return
      }
      const status = this.$store.state.metamask.status
      if (status !== 1) {
        // alert('请登陆钱包')
        this.$notify({
          title: this.$t('commom.warning'),
          message: '请登陆钱包',
          type: 'warning'
        })
      } else {
        const myProvider = this.$myProvider
        const networkVersion = myProvider.networkVersion
        if (networkVersion !== this.$ethConf.networkVersion) { // 过滤，非指定网络交易
          // alert('请将metamask网络切换至主网络')
          this.$notify({
            title: this.$t('commom.warning'),
            message: '请将metamask网络切换至主网络',
            type: 'warning'
          })
        } else {
          const contractRes = this.$myContracts && this.$myContracts.contractRes // Resonance合约地址 todo 标记 引用合约
          const myWeb3 = this.$myWeb3
          data.gasPrice = await myWeb3.eth.getGasPrice()
          data.gasLimit = this.$ethConf.gasLimit
          if (contractRes) {
            data.from = myProvider.selectedAddress
            let supportValue = ethUtils.ethToWei(value)
            data.value = ethUtils.supportReward(supportValue) // 直推投资数量的10%
            console.warn('task页面合约调用supportSubordinateAddress的参数==========', data)
            console.warn('task页面合约调用supportSubordinateAddress的参数2222222222==========', index, subordinateAddress, value)
            contractRes.methods.supportSubordinateAddress(index, subordinateAddress).send({
              from: data.from,
              gasPrice: data.gasPrice,
              gas: data.gasLimit,
              value: data.value
            }, function (error, result) {
              if (error) {
                console.error('市场扶持奖励supportSubordinateAddress发生错误', error)
              } else {
                console.warn('市场扶持奖励supportSubordinateAddress交易返回直', result) // 返回交易hash todo 扶持奖励成功，需要改变，getRecommendRecord数据
                store.dispatch('contract/getRecommendRecord')
              }
            })
          }
        }
      }
    }
  },
  created () {
    // purchase task 查询  展示, 不做提示
    // const referralAddress = this.checkUrlParam()
    // console.log('开始执行task created页面', referralAddress)
    // if (!referralAddress) {
    //   // alert('通过邀请链接的才具有参与资格')
    //   return
    // }
    const status = this.$store.state.metamask.status // 获取状态在后置导航守卫，可能会出现状态还没获取就进入了路由;解决：全局监听
    console.log('开始获取状态', status)
    if (status === 1) { // 没有对应的订阅配合，所以每次进入路由都需要获取最新数据
      console.log('开始task页面执行获取数据=============')
      this.$store.dispatch('contract/getPurchaseTasksInfo')
      this.$store.dispatch('contract/getRecommendRecord')
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../assets/variables.scss";
.tasks {
  height: 100%;
  font-size: 18px;
  .tasks-card {
    .tasks-info {
      padding: 10px 30px;
      color: $title;
      background: linear-gradient(to bottom, #efdbcb, #ffd647);
      h5 {
        text-align: left;
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        text-decoration: none;
      }
    }
    .card-middle {
      text-align: center;
      padding: 20px 10px 0;
      i {
        font-size: 70px;
      }
      .input-style {
        padding: 15px 0;
      }
    }
  }
  .box-btn {
    margin: 20px 0;
  }
  .action-btn {
    color: #409eff;
    width: 50px;
    font-size: 12px;
    font-weight: 500;
  }
}

</style>



